<template>
	<div class="container">
		<div class="game" v-if="isGame">
			<div class="head">
				<span>X:{{wins.X}}</span>
				<h1>Ходит:{{current}}</h1>
				<span>O:{{wins.O}}</span>
			</div>
			<div class="field">
				<div class="row" v-for="(row,i) in field" :key="i">
					<div
						class="ceil"
						v-for="(ceil,j) in row"
						:key="j"
						:class="{x:field[i][j]=='X'}"
						@click="move(i,j)"
					>{{ceil}}</div>
				</div>
			</div>
			<button class="reset" @click="resetGame">Начать заново</button>
			<button class="restart" @click="restartGame">Сбросить игру</button>
		</div>

		<button class="start" v-else @click="startGame">Начать игру</button>
		<audio id="audio" src="@/assets/move.mp3" />
		<Modal
			v-show="isModalVisible"
			@close="closeModal"
		>{{this.current=='X'?'O'+' Выиграл!':'X'+' Выиграл!'}}</Modal>
	</div>
</template>

<script>
import Modal from "./Modal";
export default {
	components: {
		Modal
	},
	data() {
		return {
			field: generateField(10),
			current: "X",
			isGame: false,
			wins: {
				X: 0,
				O: 0
			},
			isModalVisible: false
		};
	},
	methods: {
		restartGame() {
			this.isGame = false;
			this.current = "X";
			this.wins = { X: 0, O: 0 };
		},
		resetGame() {
			this.field = generateField(10);
		},
		startGame() {
			this.isGame = true;
			this.field = generateField(10);
		},
		move(i, j) {
			if (this.field[i][j] !== "") return;
			let audio = document.getElementById("audio");
			audio.pause();
			audio.currentTime = 0;
			audio.play();
			this.field[i][j] = this.current;
			let isWin = checkField(this.field, this.current);
			if (isWin) {
				this.isModalVisible = true;
				this.wins[this.current] += 1;
				this.resetGame();
			}
			this.current = this.current == "X" ? "O" : "X";
		},
		closeModal() {
			this.isModalVisible = false;
		}
	}
};
function generateField(n) {
	const field = [];
	for (let i = 0; i < n; i++) {
		field.push([]);
		for (let j = 0; j < n; j++) {
			field[i][j] = "";
		}
	}
	return field;
}
function checkField(field, player) {
	for (let i = 0; i < field.length; i++) {
		let countV = 0;
		let countH = 0;
		let countD = 0;
		for (let j = 0; j < field.length; j++) {
			if (field[i][j] == player && field[i][j + 1] == player) {
				countH += 1;
			}
			if (field[j][i] == player && field[j + 1] && field[j + 1][i] == player) {
				countV += 1;
			}
		}
		if (countH == 4 || countV == 4 || countD == 4) {
			return true;
		}
	}
	let Ylength = field.length;
	let Xlength = field[0].length;
	let maxLength = field.length;
	let temp;
	let returnedArray = [];
	for (let k = 0; k <= 2 * (maxLength - 1); ++k) {
		temp = [];
		for (let y = Ylength - 1; y >= 0; --y) {
			let x = k - (Ylength - y); // x= k- y
			if (x >= 0 && x < Xlength) {
				temp.push(field[x][y]);
			}
		}
		if (temp.length > 0) {
			returnedArray.push(temp);
		}
	}
	for (let i = 0; i < returnedArray.length; i++) {
		let count = 0;
		for (let j = 0; j < returnedArray[i].length; j++) {
			if (returnedArray[i][j] == player && returnedArray[i][j + 1] == player) {
				count += 1;
			}
		}
		if (count == 4) {
			return true;
		}
	}
	for (let k = 0; k <= 2 * (maxLength - 1); ++k) {
		temp = [];
		for (let y = Ylength - 1; y >= 0; --y) {
			let x = k - y;
			if (x >= 0 && x < Xlength) {
				temp.push(field[x][y]);
			}
		}
		if (temp.length > 0) {
			returnedArray.push(temp);
		}
	}
	for (let i = 0; i < returnedArray.length; i++) {
		let count = 0;
		for (let j = 0; j < returnedArray[i].length; j++) {
			if (returnedArray[i][j] == player && returnedArray[i][j + 1] == player) {
				count += 1;
			}
		}
		if (count == 4) {
			return true;
		}
	}
}
</script>

<style lang='scss'>
button {
	padding: 5px 20px;
	font-size: 24px;
	color: rgb(235, 235, 235);
	background-color: rgb(46, 95, 255);
	border: 4px solid rgb(77, 113, 233);
}
.reset {
	font-size: 14px;
	margin: 20px 0;
}
.restart {
	background-color: rgb(255, 95, 95);
	border: 4px solid rgb(255, 67, 67);
	font-size: 14px;
}
.container {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.game {
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-direction: column;
}
.start {
	margin-top: 40vh;
}
.head {
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 20px;
	span {
		font-size: 24px;
		font-weight: bold;
	}
}
.row {
	display: flex;
}
.ceil {
	margin: 1px;
	width: 50px;
	height: 50px;
	border: 2px solid rgb(80, 106, 255);
	color: rgb(67, 240, 14);
	text-align: center;
	line-height: 30px;
	font-size: 24px;
	font-weight: bold;
}
.line {
	background-color: red;
}
.x {
	color: rgb(236, 103, 103);
}
@media (max-width: 720px) {
	.ceil {
		width: 30px;
		height: 30px;
		border: 1px solid rgb(80, 106, 255);
	}
}
</style>