<script>
export default {
	name: "modal",
	methods: {
		close() {
			this.$emit("close");
		}
	}
};
</script>
<template>
	<transition name="modal-fade">
		<div class="modal-backdrop">
			<div class="modal">
				<section class="modal-body">
					<slot></slot>
				</section>
					<button type="button" @click="close">Закрыть</button>
			</div>
		</div>
	</transition>
</template>
<style>
.modal-backdrop {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: rgba(0, 0, 0, 0.3);
	display: flex;
	justify-content: center;
	align-items: center;
}

.modal {
	background: #ffffff;
	box-shadow: 2px 2px 20px 1px;
	overflow-x: auto;
	display: flex;
	flex-direction: column;
}

.modal-body {
  padding: 20px;
  text-align: center;
  font-weight: bold;
  font-size: 24px;
}

</style>